body {
  background: black;
  height: 100vh;
  margin: 0;
  display: grid;
  place-items: center;
}

.cards {
  display: flex;
}

.card {
  background: linear-gradient(to left bottom, #cd356a, #a14188, #694c8f, #334e81, #134864);
  padding: 2rem;
  margin-left: -5rem;
  box-shadow: -13px 13px 12px 2px rgba(0, 0, 0, 0.75);
  transition: 0.3s;
  color: white;
  border: 1px solid black;
  border-radius: 20px;
}

.card a {
  text-decoration: none;
  color: white;
}

.card:first-child {
  margin-left: 100px;
}

.card:last-child {
  margin-right: 100px;
}

.card:hover {
  transform: translateY(-1rem);
}

.card:hover ~ .card,
.card:focus-within ~ .card {
  transform: translate(5rem);
}
